"use client";

import React from "react";

interface CardData {
  title: string;
  description: string;
  imageUrl: string;
}

const Card = ({ title, description, imageUrl }: CardData) => {
  return (
    <div className="bg-blue shadow-lg rounded-lg overflow-hidden w-400 h-60 p-4 text-white">
      <img className="object-cover" src={imageUrl} alt={title} />
      <div className="p-4">
        <h3 className="text-xl pb-2 font-semibold">{title}</h3>
        <p className="text-sm">{description}</p>
      </div>
    </div>
  );
};

export default Card;
